* {box-sizing: border-box}

body {font-family: Verdana, sans-serif; margin:0}

.contact-us {
  position: fixed; 
  width: 100%; 
  box-shadow: 0px -8px 18px 0 rgba(0, 0, 0, 0.15);
  margin-left: 0px; 
  margin-right: 0 ; 
  background: #bee4ce;
  padding-top: 0;
  padding-bottom: 20px;
  text-align: center;
  /* z-index: 100 ; */
  bottom: 0 ; 
}

.contact-us a {
  text-decoration: none; 
  color: black; 
}

.contact-us p { 
  text-decoration: none; 
  color: black; 
}

.contact-us span {
  font-weight: bold; 
  font-style: italic;
}


.mySlides {
  display: none;
  box-shadow: 100 4px 40px 0 rgba(37, 87, 41, 1);
  padding-top: 100px; 
  padding-bottom: 100px; 
  background-color: transparent;
}

/* Slideshow container */
.slideshow-container {
  /* max-width: 1000px; */
  position: relative;
  /* margin: 1px; */
  border-color: aqua;
  overflow-y: visible; 
  /* min-width: 60%;  */
  padding-left: 20%; 
  padding-right: 20%; 
}

/* Next & previous buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  /* width: auto; */
  padding: 16px;
  margin-top: -22px;
  color: black;
  font-weight: bold;
  font-size: 25px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
  /* height: 100%;  */
}

/* Position the "next button" to the right */
.next {
  right: 20%;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  /* top: 0 ;  */
  background-color: rgba(0,0,0,0.8);
  color: #9090908a;
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.active, .dot:hover {
  background-color: #717171;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4} 
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4} 
  to {opacity: 1}
}

/* On smaller screens, decrease text size */
@media only screen and (max-width: 300px) {
  .prev, .next,.text {font-size: 11px}
}

